.wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    90deg,
    rgba(12, 18, 23, 1) 0%,
    rgba(144, 144, 144, 1) 100%
  );
}
.video {
  height: 100vh;
  object-fit: cover;
  width: 100%;
  /* opacity: ; */
  z-index: -2;
}

.title {
  position: absolute;
  top: 90%;
  left: 50%;
  width: 350px;
  transform: translate(-50%, -90%);

  font-family: 'Arvo', serif;
  font-size: 18px;
  letter-spacing: 3px;
  color: white;
}
